// fetch coordinates from Geonames API
async function getCoordinates(destination) {
    try {
      const response = await fetch(`http://api.geonames.org/searchJSON?q=${destination}&maxRows=1&username=jamela.jehad`);
      const data = await response.json();
  
      console.log("Geonames API Response:", data);
  
      // check the response if contains a valid data
      if (!data.geonames || data.geonames.length === 0) {
        throw new Error("there is no results found for this destination.");
      }
  
      return {
        lat: data.geonames[0].lat,
        lng: data.geonames[0].lng,
      };
    } catch (error) {
      console.error("Error fetching coordinates:", error);
      throw new Error("Please try again , something went wrong while fetching the coordinates .");
    }
  }
  
  // fetch weather from the Weatherbit API
  async function getWeather(lat, lng, date) {
    try {
      const WEATHERBIT_API_KEY = "229ee8f4d11a4becb352839f131a563f"; 
        const response = await fetch(`https://api.weatherbit.io/v2.0/forecast/daily?lat=${lat}&lon=${lng}&key=${WEATHERBIT_API_KEY}`);
      const data = await response.json();
  
      console.log("the Weatherbit API Response is :", data); 
  
      // check if the response contains a valid data
      if (!data.data || data.data.length === 0) {
        throw new Error("No weather data found for this location.");
      }
  
      return data.data[0]; 
    } catch (error) {
      console.error("Error fetching weather:", error);
      throw new Error("Failed to fetch weather data. Please try again.");
    }
  }
  
  // fetch image from the Pixabay API
  async function getImage(destination) {
    try {
      const PIXABAY_API_KEY = "48879614-78b92de059bfff3be17595e2b"; 
        const response = await fetch(`https://pixabay.com/api/?key=${PIXABAY_API_KEY}&q=${encodeURIComponent(destination)}&image_type=photo`);
        const data = await response.json();
      console.log("Pixabay API Response:", data); 
  
      // check if the response contains a valid data
      if (!data.hits || data.hits.length === 0) {
        throw new Error("No images found for the destination.");
      }
  
      return data.hits[0].webformatURL; 
    } catch (error) {
      console.error("Error fetching image:", error);
      throw new Error("Failed to fetch an image. Please try again.");
    }
  }
  
  // handle form submission
  async function handleSubmitting(event) {
    event.preventDefault();
    const destination = document.getElementById('destination').value;
    const date = document.getElementById('departure-date').value;
  

    const results = document.getElementById('results');
    results.innerHTML = "<p>Loading...</p>";
  
    try {
      // coordinates, weather, image
      const { lat, lng } = await getCoordinates(destination);
      const weather = await getWeather(lat, lng, date);
      const image = await getImage(destination);
  
      // results
      results.innerHTML = `
        <h2>${destination}</h2>
        <img src="${image}" alt="${destination}" style="max-width: 100%; height: auto;">
        <p>The Weather: ${weather.weather.description}</p>
        <p>The Temperature: ${weather.temp}°C</p>
      `;
    } catch (error) {
      results.innerHTML = `<p style="color: red;">Error: ${error.message}</p>`;
    }
  }
  
  document.getElementById('travel-form').addEventListener('submit', handleSubmitting);